
export default {
  randomInt(min,max) {
    return min + parseInt(Math.random()*(max-min + 1))
  },
  radomArrayInt(min,max, length) {
    let res = []
    for (let i = 0; i < length; i++) {
      let tmp = min +  parseInt(Math.random()*(max-min))
      res.push(tmp)
    }
    return res
  },
  radomArrayPoint(min,max, length) {
    let res = []
    for (let i = 0; i < length; i++) {
      let tmp = Number((Math.random() * max).toFixed(3))
      res.push(tmp)
    }
    return res
  },
  pollutantsWithValue(min,max) {
    let pollutants = this.genPollutants()
    let values = this.radomArrayInt(min,max, pollutants.length)
    return pollutants.map((v,i) => ({
      value: values[i],
      name: v
    }))
  },
  genPollutants() {
    var nameList = [
      "","","","","","","","",""
    ];
    // nameList.length = 6
    return nameList.sort(() => Math.random() - 0.5)
  },
  genCompanys(length) {
    var nameList = [
      '',
      '',
      '',
      '',
      '',
      '',
      '',
      '',
    ];
    nameList.sort(() => Math.random() - 0.5)
    return nameList.slice(0,length)
  },
  genTotals(length) {
    var nameList = [
      'SO2',
      'NOx',
      'COD',
      'NH3-N',
      'VOCs',
      '',
      '',
    ];
    nameList.sort(() => Math.random() - 0.5)
    return nameList.slice(0,length)
  },
  genCrafts(length) {
    var nameList = [
      '',
      '',
      '',
      '',
      '',
      '',
      '',
      '',
    ];
    nameList.sort(() => Math.random() - 0.5)
    return nameList.slice(0,length)
  },
  getMax(arr) {
    let max;
    arr.forEach(v => {
      if(!max || v.value > max) {
        max = v.value
      }
    })
    return max
  }
}