import AMapLoader from "@amap/amap-jsapi-loader";

export default async function MapLoader() {
  if (window.AMap) {
    return window.AMap
  } else {
    try {
      window.AMap = await AMapLoader.load({
        "key": "3585fda7195df2b08c73143bbc831307",              // WebKey load 
        "version": "1.4.15",   //  JSAPI  1.4.15
        "plugins": ["AMap.ControlBar"],          // 'AMap.Scale'
        "AMapUI": {             //  AMapUI
          "version": '1.1',   // AMapUI  1.1
          "plugins": [],       //  AMapUI ui
        },
        "Loca": {                //  Loca 
          "version": '1.3.2'  // Loca  1.3.2
        },
      })
      return window.AMap
    } catch (e) {
      console.log(e)
    }
    return window.AMap
  }
}
